.header {
  background-color: #f3fff9;
  padding-top: 40px;
  padding-left: 60px;
  padding-right: 60px;
  background-image: url('./images/header.png');
  background-size: 450px;
  background-repeat: no-repeat;
  background-position-x: right;
  background-position-y: 120px;
}
.nav {
  height: 80px;
}
.intro {
  padding-bottom: 70px;
}

h1 {
  font-size: 60px;
  margin: 0;
}

p {
  font-size: 24px;
}
button {
  background-color: rgb(50, 200, 80);
  border-radius: 14px;
  padding: 10px 40px;
  border: 3px solid transparent;
  color: white;
  font-size: 28px;
  font-family: 'Circular Std';
  outline: none;
}

button:hover {
  cursor: pointer;
  color: rgb(50, 200, 80);
  background-color: white;
  border: 3px solid rgb(50, 200, 80);
}
.content {
  padding-left: 120px;
  padding-right: 120px;
  padding-bottom: 120px;
  padding-top: 30px;
}

.MuiGrid-item.terms {
  margin-top: 90px;
  background-color: #f3fff9;
  padding: 30px 40px;
  text-align: center;
}
.terms p {
  font-size: 15px;
}
